.scroll_nav {
    position: fixed;
    background: transparent;
    z-index: 99;
    left: 30px;
    text-align: center;
    top: 35%;
}
.scroll_nav .navbar-nav>li {
    float: none;
}
#myNavbar>li.active a {
    color: #003267 !important;
    font-size: 20px !important;
}
.scroll_nav a {
    font-weight: 900;
    display: inline-block;
    width: 100%;
    float: left;
    -webkit-transition: font-size .2s ease,color .2s ease;
    transition: font-size .2s ease,color .2s ease;
    color: #0088ab;
    font-size: 20px;
    text-decoration: none;
    line-height: 24px;
}
.scroll_nav .nav>li>a {
    position: relative;
    display: block;
    padding: 0px 15px;
}
.scroll_nav .nav>li>a:focus
{
    background-color: inherit;
}
