@keyframes fadeinout {
  0%,100% { opacity: 0; }
  100% { opacity: 1; }
}

@keyframes fadeIn {
	0%,100% { opacity: 0; }
  50% { opacity: 1; }
    from {
        opacity: 0;
        transform: translate3d(0, -20%, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}
@keyframes fadeout {
    from {
        opacity: 1;
        transform: translate3d(0, -20%, 0);
    }
    to {
        opacity: 1;
        transform: translate3d(0, 0, 0);
    }
}


